ul li {
  padding: 40px;
  list-style: none;
  position: relative;
}

li:not(:first-child):before {
  content: "||";
  position: absolute;
  left: -10px;
}

ul li a {
  color: #000;
  font-size: 20px;
  text-decoration: none;
  position: relative;
}

a:after {
  content: "";
  width: 100%;
  height: .1em;
  opacity: 0;
  background-color: #ff69b4;
  transition: opacity .3s, transform .3s;
  position: absolute;
  bottom: 0;
  left: 0;
}

a:hover:after, a:focus:after {
  opacity: 1;
  transform: translate3d(0, .2em, 0);
}

